@import url(../vendor/normalize.css);
@import url(../vendor/fonts/fonts.css);

html {
  scroll-behavior: smooth;
}
  
body {
  background-color: #fff;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  box-sizing: border-box;
  color: #000;
  
  max-width: 1280px;
  height: 100vh;
  margin: 0 auto;
  box-sizing: border-box;
}
  
.list {
  padding: 0;
  list-style-type: none;
  margin-block-start: 0;
  margin-block-end: 0;
}
  
.link {
  color: #000;
  text-decoration: none;
  transition: 0.3s;
}
  
.link:hover {
  opacity: 0.7;
  transition: 0.3s;
}
  
.button {
  cursor: pointer;
  padding: 0;
  border: none;
  background-color: #000;
  transition: 0.3s;
}
  
.button:hover {
  opacity: 0.8;
  transition: 0.3s;
}
  