.header__burger {
  width: 38px;
  height: 38px;
  margin: 0;
  display: none;
  box-sizing: border-box;
  object-fit: contain;
  background: none;
/*  background-image: url(../../images/burger.svg); */
}
  
@media screen and (max-width: 768px) {
  .header__burger {
    display: block;
    background-image: url(../../images/burger.svg);
    border: none;
  }
}
  